.btn {
    background-color: #3b5998;
    border-radius: 50%;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Effetto hover che solleva l'icona di 2px */
.btn:hover {
    transform: translateY(-2px); /* Alza l'icona di 2px */
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.2); /* Ombra più marcata durante l'hover */
}

.bg-body-footer {
    background-color: #0a822e;
}

.icon-container {
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: space-around;
}

.circle {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 20px;
}